<template>
  <v-app id="inspire">
    <v-main class="bg-color">
      <v-container>
        <v-row>
          <v-col
            cols="3"
          >
            <v-sheet
              rounded="lg"
              color="bg-color"
            >
            <v-row>
                <v-col cols="3">
                    <v-avatar
                      class="mt-2"
                      color="primary"
                      size="56"
                      tile
                    >
                      <span class="initials" v-if="!user.photo">{{initials}}</span>
                      <img :src="user.photo" v-if="user.photo">
                    </v-avatar>
                </v-col>
                <v-col class="mt-2">
                    <h3>Vugar Eyvazov</h3>
                    <p><a href="#" class="edit-link">View Profile</a></p>
                </v-col>
            </v-row>
            <v-divider></v-divider>
            </v-sheet>
          </v-col>

          <v-col
            cols="6"
          >
            <v-sheet
              rounded="lg"
              class="bg-color"
            >
              <router-view></router-view>
            </v-sheet>
          </v-col>

          <v-col
            cols="3"
          >
            <v-sheet
              rounded="lg"
              class="bg-color"
            >
              <!--  -->
            </v-sheet>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
 import { mapActions, mapGetters } from 'vuex';
 export default {
   name: 'LayoutDefault',
   data: () => ({ 
   }),
   computed: {
     ...mapGetters('profile', [
       'user',
       'initials'
     ])
   },
   methods: {
     ...mapActions(['initialLoad']),
     ...mapActions('profile', [
       'getMemberData',
       'getMemberPhoto'
     ]),
     ...mapActions('admin', [
       'getPollData'
     ]),
     goTo(routeUrl){
         this.$router.push(routeUrl);
     }
   },
   created: async function(){
     await this.getMemberData();
     await this.getMemberPhoto();
     await this.getPollData();
     await this.initialLoad();
   },
 }
</script>

<style lang="scss" scoped>
    .bg-color{
        background-color: #f7f8f8;
    }
    .initials {
        color: white
    }
    .edit-link{
        text-decoration: unset;
    }
</style>